<template>
  <LoadingSpinnerPanel v-if="busy" />
  <template v-else>
    <component
      :is="BLink"
      v-for="(link, linkIndex) in _links"
      :key="`sidebar-link-${linkIndex + 1}`"
      :to="link.to"
      :href="link.url"
      :target="link.url ? link.target || '_blank' : link.target || undefined"
    >
      <div :class="['border-bottom border-success p-2', { 'mb-1 border-top': linkIndex === 0 }]">
        {{ link.title }}
      </div>
    </component>
  </template>
</template>

<script setup>
  import { BLink } from "bootstrap-vue-next";
  import LoadingSpinnerPanel from "./LoadingSpinnerPanel.vue";

  const props = defineProps(["busy", "links"]);

  const _links = computed(() => (props.links || []).filter(l => !l.hidden));
</script>
